<template>
    <div class="relative container mx-auto px-40 bg-gray-200">
      <HomeLink />
      <SearchBanner />
      <NavMenu />
      <SearchPage />  
      <ImageDetails />
      <SearchBPI />
      {{ bpiList }}
      <SimpleFooter />
    </div>
  </template>
    
    <script>
   
    import SearchBanner from '../components/SearchBanner.vue';
    import NavMenu from '../components/NavMenu.vue';
    import SearchPage from '../components/SearchPage.vue';
    import SimpleFooter from '../components/SimpleFooter.vue';
    import HomeLink from '../components/HomeLink.vue';
    import ImageDetails from '../components/ImageDetails.vue'
    import SearchBPI from '@/components/SearchBPI.vue';

    import axios from 'axios'
  
    export default {
      setup() {
          return {};
      },
      components: { 
        HomeLink,
        SearchBanner, 
        NavMenu,
        SearchPage,
        ImageDetails,
        SearchBPI,
        SimpleFooter
      },
      data() {
          return { 
             bpiList : {}
          }
      },
      methods: {
            async fetchBPIList() {
                const { data } = await axios.get("http://localhost:3000/api/bpi_cat");
                this.bpiList = data;
            }
        },
        beforeMount() {
          this.fetchBPIList();
        },
  }
    </script>